"use client";


import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import { CardContent } from "@/components/ui/card";

export default function cashflowChart({ data }) {
  return (
    <CardContent className="p-4">
      <h2 className="text-lg font-semibold mb-2">Monthly Income vs Expense</h2>
      <ResponsiveContainer width="100%" height={300}>
        <LineChart
          data={data}
          margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="month" />
          <YAxis />
          <Tooltip formatter={(v) => `â‚±${v.toLocaleString()}`} />
          <Legend />
          <Line type="monotone" dataKey="income" stroke="#16a34a" name="Income" />
          <Line type="monotone" dataKey="expense" stroke="#dc2626" name="Expense" />
          <Line type="monotone" dataKey="net" stroke="#2563eb" name="Net" />
        </LineChart>
      </ResponsiveContainer>
    </CardContent>
  );
}
